#div-intro #div-intro-contents {
    border: 1px solid lightgrey;
    width: 500px;
    height: 200px;
    margin-left: 50px;
    border-radius: 50px;
    background-color: white;
}

#div-intro-contents div {
    padding: 25px;
}

#div-intro-img-wrapper img {
    border: 1px solid lightgrey;
    border-radius: 50%;
}

#div-intro-contents-wrapper, #div-intro-img-wrapper {
    display: flex;
    align-items: center;
}

.p-intro-title-header {
    font-family: PureunJeonnam;
    font-size: 30px;
}

.p-intro-title {
    font-family: PureunJeonnam;
    font-size: 20px;
}

@media screen and (max-width: 524px) {
    #div-intro #div-intro-contents {
        width: 100%;
        margin-left: 0px;
    }
}

@media screen and (max-width: 960px) {
    /* 모바일에 사용될 스트일 시트를 여기에 작성합니다. */
    #div-intro-img-wrapper {
        justify-content: center;
    }

    #div-intro-contents-wrapper {
        justify-content: center;
    }

    #div-intro-img-wrapper img {
        margin-top: 150px;
        margin-bottom: 30px;
    }

    #div-intro #div-intro-contents {
        width: 500px;
        margin-left: 0px;
    }
}

@media screen and (min-width: 960px) {
    /* 데스크탑에서 사용될 스타일을 여기에 작성합니다. */
    #div-intro-img-wrapper {
        justify-content: end;
        animation-duration: 1s;
        animation-name: slideIn;
    }

    #div-intro-contents-wrapper {
        justify-content: start;
        animation-duration: 1s;
        animation-name: slideIn;
    }

    #div-intro .row {
        height: 95vh;
    }
}

@media screen and (max-width: 768px) {
    #div-intro-contents {
        text-align: center;
    }
}

@media screen and (max-width: 425px) {
    #div-intro-contents div {
        padding: 20px;
    }
}

@media screen and (max-width: 375px) {
    .p-intro-title-header {
        font-family: PureunJeonnam;
        font-size: 28px;
        margin-bottom: 30px;
    }

    .p-intro-title {
        font-family: PureunJeonnam;
        font-size: 17px;
    }
}

@media screen and (max-width: 320px) {
    #div-intro-contents div {
        padding: 15px;
    }

    .p-intro-title-header {
        font-family: PureunJeonnam;
        font-size: 26px;
        margin-bottom: 20px;
    }

    .p-intro-title {
        font-family: PureunJeonnam;
        font-size: 17px;
    }
}

@keyframes slideIn {
    from {
        margin-bottom:100px;
    }

    to {
        margin-bottom: 0px;
    }
}