/* Icons */
@font-face
{
    font-family: 'slick';
    font-weight: normal;
    font-style: normal;
}
/* Arrows */
.slick-prev,
.slick-next
{
    font-size: 0;
    line-height: 0;

    position: absolute;
    top: 50%;

    display: block;

    width: 64px;
    height: 64px;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);

    cursor: pointer;

    color: black;
    border: none;
    outline: none;

    z-index: 1000;
}

.slick-prev {
    background: url("./slickImages/arrow-left.png") no-repeat;
    opacity: .2;
}

.slick-next {
    background: url("./slickImages/arrow-right.png") no-repeat;
    opacity: .2;
}

.slick-next:hover,
.slick-next:focus {
    background: url("./slickImages/arrow-right-hover.png");
    opacity: 1;
}

.slick-prev:hover,
.slick-prev:focus {
    background: url("./slickImages/arrow-left-hover.png");
    opacity: 1;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before
{
    opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before
{
    opacity: .25;
}

.slick-prev:before,
.slick-next:before
{
    font-family: 'slick';
    font-size: 0px;
    line-height: 0;

    opacity: .75;
    color: white;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-prev
{
    left: -60px;
}
[dir='rtl'] .slick-prev
{
    right: 0px;
}

.slick-next
{
    right: -60px;
}
[dir='rtl'] .slick-next
{
    right: 0;
    left: 0px;
}

@media screen and (max-width: 426px) {
    /* 모바일에 사용될 스트일 시트를 여기에 작성합니다. */
    .slick-prev
    {
        left: 30px;
        width: 48px;
        height: 48px;
        background: url("./slickImages/arrow-left-48.png") no-repeat;
    }
    .slick-next
    {
        right: 20px;
        width: 48px;
        height: 48px;
        background: url("./slickImages/arrow-right-48.png") no-repeat;
    }
    .slick-next:hover,
    .slick-next:focus {
        background: url("./slickImages/arrow-right-hover-48.png");
    }

    .slick-prev:hover,
    .slick-prev:focus {
        background: url("./slickImages/arrow-left-hover-48.png");
    }
}

/* Dots */
.slick-dotted.slick-slider
{
    margin-bottom: 30px;
}

.slick-dots
{
    position: absolute;
    bottom: -25px;

    display: block;

    width: 100%;
    padding: 0;
    margin: 0;

    list-style: none;

    text-align: center;
}
.slick-dots li
{
    position: relative;

    display: inline-block;

    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;

    cursor: pointer;
}
.slick-dots li button
{
    font-size: 0;
    line-height: 0;

    display: block;

    width: 20px;
    height: 20px;
    padding: 5px;

    cursor: pointer;

    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
}
.slick-dots li button:hover,
.slick-dots li button:focus
{
    outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before
{
    opacity: 1;
}
.slick-dots li button:before
{
    font-family: 'slick';
    font-size: 6px;
    line-height: 20px;

    position: absolute;
    top: 0;
    left: 0;

    width: 20px;
    height: 20px;

    content: '•';
    text-align: center;

    opacity: .25;
    color: black;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before
{
    opacity: .75;
    color: black;
}
